@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
    height: 100%;
    width: 100%;
    overflow: hidden;
    background-color: #130f40;
    font-family: "Quicksand", sans-serif;
}

@media screen and (min-width: 481px) and (max-width: 1024px) {
    #root {
        height: 100%;
        padding: 0 15%;
    }

    .container {
        background-image: url("https://wallpapercave.com/wp/wp2234456.jpg");
        height: 100vh;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        padding: 5%;
        z-index: 0;
    }

    .main {
        padding: 20px;
        z-index: 99;
        box-shadow: "0px 0px 15px rgba(0, 0, 0, 0.25)";
        background-color: rgb(255, 255, 255, 0.9);
        padding: 20px;
        overflow: auto;
        transition: linear 0.3s;
        height: 50%;
        border-radius: 10px;
    }

    .main-admin {
        position: relative;
        margin-top: -150px;
        padding: 20px;
        z-index: 9990;
        box-shadow: "0px 0px 15px rgba(0, 0, 0, 0.25)";
        background-color: rgb(255, 255, 255, 0.9);
        padding: 20px;
        overflow: auto;
        transition: linear 0.3s;
        height: 80%;
        border-radius: 10px;
        overflow: hidden;
    }

    .table-container{
        padding: 20px;
        z-index: 99;
        box-shadow: "0px 0px 15px rgba(0, 0, 0, 0.25)";
        background-color: rgb(255, 255, 255, 0.9);
        margin: 20px 0px;
        overflow: auto;
        transition: linear 0.3s;
        height: 65%;
        border-radius: 10px;
    }

    .header {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 15px;
        background-color: black;
        color: white;
        font-size: 16px;
        font-weight: bold;
        border-radius: 10px 10px;
    }

    .footer{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 15px;
        background-color: #273c75;
        color: white;
        font-size: 16px;
        font-weight: bold;
        border-radius: 10px 10px;
    }

    .footer-back{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 15px;
        background-color: #e74c3c;
        color: white;
        font-size: 16px;
        font-weight: bold;
        border-radius: 10px 10px;
    }

    .tb {
        border-collapse: collapse;
        margin-top: 20px;
        font-size: 12px;
        font-weight: bold;
        width: 100%;
    }
    
    .tb td,
    .tb th {
        padding: 8px;
    }
    
    .tb tr:hover {
        background-color: #ddd;
    }
    
    .tb th {
        padding-top: 12px;
        padding-bottom: 12px;
        text-align: left;
        background-color: #04aa6d;
        color: white;
    }

    .logo {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
    }
    
    .logo img {
        position: absolute;
        width: 80%;
        z-index: 1;
        top: 0px;
    }
    
    .bg {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
    }
    
    .bg img {
        width: 60%;
        z-index: 2;
    }

    .login-container{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 15px;
        height: 80%;
    }

    .login-container input{
        width: 100%;
        margin: 5px 0px;
        font-size: 16px;
        border-radius: 10px 10px;
        border: 1px solid black;
        padding: 13px;
    }

    .login-container button{
        width: 100%;
        margin: 5px 0px;
        font-size: 16px;
        border-radius: 10px 10px;
        border: 0px solid black;
        padding: 13px;
        background-color: #2d3436;
        color: #fff;
        font-weight: bold;
    }

    .open{
        font-weight: bold;
        color: #04aa6d;
        font-style: italic;
    }

    .close{
        font-weight: bold;
        color: red;
        font-style: italic;
    }

    .modal-container{
        position: absolute;
        display: none;
        width: 100%;
        height: 100%;
        top: 1500px;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgb(255, 255, 255);
        border-radius: 15px;
        flex-direction: column;
        transition: linear 0.3s;
    }

    .modal-header{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 15px;
        background-color: black;
        color: white;
        font-size: 16px;
        font-weight: bold;
        border-radius: 10px 10px;
        width: 75%;
        margin: 20px 0px;
    }

    .modal-body{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 100%;
        width: 75%;
    }

    .modal-body input{
        width: 90%;
        margin: 5px 0px;
        font-size: 16px;
        border-radius: 10px 10px;
        border: 1px solid black;
        padding: 13px;
    }

    .modal-body button{
        width: 100%;
        margin: 5px 0px;
        font-size: 16px;
        border-radius: 10px 10px;
        border: 0px solid black;
        padding: 13px;
        background-color: #2d3436;
        color: #fff;
        font-weight: bold;
    }
    

}

